@import "mediaQuery.scss";

#header_box_r_balance_price {
  display: inline-block;
  background-size: 13px 13px;
  padding-left: 23px;
  background-repeat: no-repeat;
  background-color: rgba(0, 60, 60, 1);
  border: 1px solid #066;
  color: #BDD;
  padding: 10px;
  margin-left: 15px;
  text-decoration: none;
  font-size: 13px;
  line-height: 100%;
  
  span {
    font-size: 13px;
    line-height: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-weight: normal;
  }
  .idol{
    background-image: url(../../../resource/images/icon_idol.png);
    background-size: 0.8vw 0.8vw;
    padding-left: 2vw;
  }
  .eth {
    background-image: url(../../../resource/images/icon_eth.png);
    background-size: 0.6vw 0.8vw;
    padding-left: 2vw;
  }
}
