@import "mediaQuery.scss";

.number {
  display: flex;
  justify-content: space-between;

  @include mq() {
    margin-top: 15px;
  }
  @include mq(sm) {
    margin-top: 10px;
  }

  span {
    display: inline-block;
    line-height: 100%;
    color: #666;

    &:nth-child(2) {
      font-weight: 500;
      color: #000;
    }
  }
}
