@import "mediaQuery.scss";

#header_box_r_mobile {
  line-height: 0;
  border: 1px solid #066;
  background-color: rgba(0, 60, 60, 1);
  margin-left: 15px;

  @include mq() {
    display: none;
  }
  @include mq(sm) {
    display: inline-block;
  }
}

@include mq(sm) {
  #header_box_r_mobile_switch {
    display: none;
  }
  
  #header_box_r_mobile_button {
    display: inline-block;
    opacity: 0.5;
    transition: opacity 0.25s;
    cursor: pointer;
    width: 13px;
    height: 13px;
    background-position: 10px 10px;
    background-image: url(~image/icon_mobile.png);
    background-size: 13px 13px;
    background-repeat: no-repeat;
    padding: 10px;
  }
  
  #header_box_r_mobile_nav {
    position: fixed;
    left: 0;
    height: 0;
    width: 100%;
    overflow: hidden;
    margin-top: 16px;
    transition: height 0.25s;
  
    hr {
      margin: 0;
      padding: 0;
      border: 0;
      border-top: 2px dotted #333;
      margin: 10px 0;
    }
  }
  
  #header_box_r_mobile_switch:checked ~ #header_box_r_mobile_nav {
    width: 100%;
    height: 100%;
    transition: height 0.25s;
  }
  
  #header_box_r_mobile_nav label {
    display: block;
    cursor: pointer;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
  
    a {
      display: block;
      text-decoration: none;
      background-color: rgba(0, 60, 60, 0.25);
      padding: 15px;
      line-height: 100%;
      font-size: 14px;
      border-bottom: 1px solid #011;
      letter-spacing: 2px;
      color: #BDD;
  
      &:hover {
        border-bottom: 1px solid #066;
        background-color: rgba(0, 60, 60, 1);
      }
    }
  }
  
  #header_box_r_mobile_nav_wallet {
    padding: 15px;
    background-color: rgba(0, 60, 60, 0.5);
  
    span {
      &:nth-child(1) {
        display: block;
        font-size: 11px;
        line-height: 100%;
        color: #BDD;
        font-weight: 600;
      }
  
      &:nth-child(2) {
        display: block;
        font-size: 11px;
        margin-top: 10px;
        line-height: 100%;
        color: #BDD;
      }
    }
  }
}
