@charset "utf-8";
@import "mediaQuery.scss";

/* BASE ==================================================================================================== */

html {
  width: 100%;
  @include mq() {
    height: 100%;
  }
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
	font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Helvetica", "Arial", sans-serif;
	letter-spacing: 1px;
  @include mq() {
    height: 100%;
    background: #000000;
    background: -moz-linear-gradient(left, #000000 0%, #000000 50%, #eeeeee 50%, #eeeeee 50%, #eeeeee 100%);
    background: -webkit-linear-gradient(left, #000000 0%, #000000 50%, #eeeeee 50%, #eeeeee 50%, #eeeeee 100%);
    background: linear-gradient(to right, #000000 0%, #000000 50%, #eeeeee 50%, #eeeeee 50%, #eeeeee 100%);
    filter: "progid: DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#eeeeee', GradientType=1 )";
  }
  @include mq(sm) {
    background-color: #E5E5E5;
  }
}

#root {
  height: 100%;
}

div, a, h1, h2, h3, h4, p, span, input, button {
  box-sizing: border-box;
}

input {
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  letter-spacing: 1px;
  box-sizing: border-box;
  max-width: 100%;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -webkit-appearance: none;

  @include mq(sm) {
    line-height: 100%;
    font-size: 13px;
    border-radius: 0;
    margin: 0;
    padding: 0;
  }

  &::-webkit-input-placeholder {
    padding-top: 2px;
  }

  &:focus {
    outline: none;
  }

  &[type="number"] {
    &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }

    -moz-appearance: textfield;
  }

  &[type="button"] {
    margin: 0;
    padding: 0;
  }
}

textarea {
  @include mq(sm) {
    border-radius: 0;
    margin: 0;
    padding: 0;
  }
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -webkit-appearance: none;
  max-width: 100%;
}

img, input {
  vertical-align: bottom;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

/* LAYOUT ==================================================================================================== */

#container {
  margin: 0 auto;
  @include mq() {
    width: 100vw;
    min-height: 100%;
    display: flex;
  }
  @include mq(sm) {
    width: 100%;
  }
}

/* CONTENT ==================================================================================================== */

#content {
  width: 100%;
  background-color: #EEE;
  @include mq() {
    min-height: 100%;
  }
  @include mq(sm) {
    padding-top: 65px;
  }
}

#content_box {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 80vw;

  @include mq() {
    padding: 20px 0 6px 20px;
  }
  @include mq(sm) {
    padding: 10px;
  }
}

.content_box_1, .content_box_2, .content_box_3, .content_box_4, .content_box_modal {
  background-color: #FFF;
  box-shadow: 0px 0px 2px 1px #DDD;
  border-radius: 2px;

  @include mq() {
    margin-top: 20px;
    width: 300px;
  }
  @include mq(sm) {
    margin-top: 10px;
    width: 100%;
  }

  hr {
    margin: 0;
    padding: 0;
    border: 0;
    border-top: 2px dotted #E5E5E5;
    @include mq() {
      margin: 15px 0;
    }
    @include mq(sm) {
      margin: 10px 0;
    }
  }
}

.content_box_1 {
  font-size: 13px;
  width: 18vw;
  line-height: 145%;
}

@include mq() {
  .content_box_2 {
    width: 39vw;
  }

  .content_box_3 {
    width: 60vw;
  }

  .content_box_4 {
    width: 80vw;
  }

  .content_box_modal {
    width: 50vw;
  }
}

