// TAB ===========
.react-tabs {
  width: 100%;
  &__tab-panel {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid #F5F5F5;
  }
  &__tab-list {
    width: 100%;
    display: flex;    
    border-bottom: 1px solid #DDD;
    @include mq() {
      margin-top: 20px;
    }
    @include mq(sm) {
      margin-top: 10px;
    }
  }
  &__tab {
    font-size: 13px;
    margin-left: 10px;
    font-weight: 600;
    color: #066;
    border-radius: 2px 2px 00;
    background-color: #FFF;
    display: inline-block;
    border-top: 1px solid #DDD;
    border-left: 1px solid #DDD;
    border-right: 1px solid #DDD;
    padding: 10px 16px;
    text-decoration: none;
    line-height: 100%;

    &:nth-child(1){
      margin-left: 0;
    }

    &--selected{
      font-size: 13px;
      font-weight: 600;
      color: #066;
      border-radius: 2px 2px 0 0;
      background-color: #DFE;
      display: inline-block;
      border-top: 1px solid #BDC;
      border-left: 1px solid #BDC;
      border-right: 1px solid #BDC;
      padding: 10px 16px;
      text-decoration: none;
      line-height: 100%;
    }
  }
}

