    padding-left: 28px;
  }
}

.box {
  font-size: 13px;
  border-bottom: 1px solid #E5E5E5;

  @include mq() {
    padding: 15px;
  }
  @include mq(sm) {
    padding: 10px;
  }
}

.button {
  @include mq() {
    padding: 15px;
  }
  @include mq(sm) {
    padding: 10px;
  }

  .tx {
    display: block;
    text-decoration: none;
    border: 1px solid #0A8;
    background-color: #CED;
    padding: 1px;
    border-radius: 2px;
  }

  &:hover {
    cursor: pointer;
  }

  &:active {
    color:#000000;
    opacity:0.5;
  }


  span {
    border-radius: 2px;
    display: block;
    text-decoration: none;
    background: #009977;
    background: -moz-linear-gradient(top, #009977 0%, #00aa88 2.5%, #00aa88 50%, #009977 50%, #009977 50%, #009977 97.5%, #008866 100%);
    background: -webkit-linear-gradient(top, #009977 0%, #00aa88 2.5%, #00aa88 50%, #009977 50%, #009977 50%, #009977 97.5%, #008866 100%);
    background: linear-gradient(to bottom, #009977 0%, #00aa88 2.5%, #00aa88 50%, #009977 50%, #009977 50%, #009977 97.5%, #008866 100%);
    filter: "progid: DXImageTransform.Microsoft.gradient( startColorstr='#009977', endColorstr='#008866', GradientType=0 )";
    color: #fff;
    font-size: 13px;
    text-shadow: 2px 2px 1px #086;
    padding: 15px 0;
    font-weight: 600;
    line-height: 100%;
    text-align: center;

    &:hover {
      background: #00aa88;
      background: -moz-linear-gradient(top, #00aa88 0%, #00bb99 2.5%, #00bb99 50%, #00aa88 50%, #00aa88 50%, #00aa88 97.5%, #008866 100%);
      background: -webkit-linear-gradient(top, #00aa88 0%, #00bb99 2.5%, #00bb99 50%, #00aa88 50%, #00aa88 50%, #00aa88 97.5%, #008866 100%);
      background: linear-gradient(to bottom, #00aa88 0%, #00bb99 2.5%, #00bb99 50%, #00aa88 50%, #00aa88 50%, #00aa88 97.5%, #008866 100%);
        filter: "progid: DXImageTransform.Microsoft.gradient( startColorstr='#11aa88', endColorstr='#19977', GradientType=0 )";
    }

    &[aria-disabled="true"] {
      color: #aaa;
      background: #007755;
      background: -moz-linear-gradient(top, #007755 0%, #008866 2.5%, #008866 50%, #007755 50%, #007755 50%, #007755 97.5%, #008866 100%);
      background: -webkit-linear-gradient(top, #007755 0%, #008866 2.5%, #008866 50%, #007755 50%, #007755 50%, #007755 97.5%, #008866 100%);
      background: linear-gradient(to bottom, #007755 0%, #008866 2.5%, #008866 50%, #007755 50%, #007755 50%, #007755 97.5%, #008866 100%);
      filter: "progid: DXImageTransform.Microsoft.gradient( startColorstr='#007755', endColorstr='#008866', GradientType=0 )";
      text-shadow: 0px 0px 0px #086;
    }
  }
}

