        background-image: url(resource/images/icon_lien.png);
        background-size: 20px 20px;
        background-position: top left;
        background-repeat: no-repeat;
        padding-left: 35px;
      }

      &.eth {
        background-image: url(resource/images/icon_eth.png);
        background-size: 12px 20px;
        background-position: top left;
        background-repeat: no-repeat;
        padding-left: 27px;
      }

      &.coin {
        background-image: url(resource/images/icon_coin.png);
        background-size: 20px 20px;
        background-position: top left;
        background-repeat: no-repeat;
        padding-left: 35px;
      }

      &:nth-child(1) {
        color: #666;
        display: block;
        line-height: 100%;
      }

      &:nth-child(2) {
        font-weight: 600;
        font-size: 20px;
        display: inline-block;
        line-height: 100%;
        margin-top: 15px;
      }

      &.expand_frame {
        color: #666;
        display: block;
        line-height: 100%;
        padding-top: 15px;
      }
    }
  }
}
