@import "mediaQuery.scss";

.idol{
  background-image: url(../../../resource/images/icon_idol.png);
  background-position: top left;
  background-repeat: no-repeat;
  @include mq() {
    background-size: 16px 16px;
    padding-left: 39px;
  }
  @include mq(sm) {
    background-size: 20px 20px;
    padding-left: 35px;
  }
}

