@import "mediaQuery.scss";

#content_box_title {
  border-left: 10px solid #099;
  background: #ffffff;
  background: -moz-linear-gradient(left, #ffffff 0%, #ffffff 50%, #eeeeee 100%);
  background: -webkit-linear-gradient(left, #ffffff 0%, #ffffff 50%, #eeeeee 100%);
  background: linear-gradient(to right, #ffffff 0%, #ffffff 50%, #eeeeee 100%);
  filter: "progid: DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#eeeeee', GradientType=1 )";
  padding: 10px 0 10px 20px;
  line-height: 100%;
  font-weight: 600;
  width: 100%;

  @include mq() {
    font-size: 24px;
  }
  @include mq(sm) {
    font-size: 20px;
  }
}
