@import "mediaQuery.scss";

#nav {
  @include mq() {
    padding-top: 20px;
    background-color: #000;
    width: 5vw;
    min-width: 200px;
    min-height: 100%;

    a {
      font-size: 15px;
      text-decoration: none;
      line-height: 100%;
      display: block;
      line-height: 100%;
      padding: 20px;
      color: #BBB;
      text-align: right;
      text-shadow: 0px 0px 0px #0FD;

      &:hover {
        text-shadow: 0px 0px 2.5px #0FD;
        color: #FFF;
        transition: text-shadow, color 0.25s;
      }
    }

    hr {
      background: #000000;
      background: -moz-linear-gradient(left, #000000 0%, #003333 50%, #006666 80%, #006666 80%, #009999 85%, #006666 90%, #003333 100%);
      background: -webkit-linear-gradient(left, #000000 0%, #003333 50%, #006666 80%, #006666 80%, #009999 85%, #006666 90%, #003333 100%);
      background: linear-gradient(to right, #000000 0%, #003333 50%, #006666 80%, #006666 80%, #009999 85%, #006666 90%, #003333 100%);
      filter: "progid: DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#003333', GradientType=1 )";
      margin: 0;
      padding: 0;
      border: 0;
      height: 1px;
    }
  }
  @include mq(sm) {
    display: none;
  }
}

@include mq() {
  #nav_sns {
    padding: 10px;
    display: flex;
    justify-content: flex-end;
    margin-left: 15px;
  
    a {
      background-color: rgba(0, 60, 60, 0.25);
      border: 1px solid #022;
      padding: 5px;
      margin-left: 10px;
    }
  
    img {
      width: 20px;
      height: 20px;
    }
  
    a:hover {
      border: 1px solid #066;
      background-color: rgba(0, 60, 60, 1);
      transition: border 0.25s, background-color 0.25s;
    }
  }
}
