@import "mediaQuery.scss";

.price {
  display: flex;
  justify-content: space-between;

  @include mq() {
    margin-top: 20px;
    padding: 10px;
  }
  @include mq(sm) {
    margin-top: 18px;
    padding: 10px;
  }

  span {
    display: inline-block;
    line-height: 100%;
    font-weight: 1200;
    color: #666;

    &:nth-child(2) {
      font-weight: 1200;
      color: #000;
    }
  }
}
