@import "mediaQuery.scss";

.radio {
  display: flex;
  line-height: 100%;
  border-top: 2px;

  @include mq() {
    margin-top: 15px;
  }
  @include mq(sm) {
    margin-top: 10px;
  }

  .item {
    margin-left: 15px;

    &:nth-child(1) {
      margin-left: 0;
    }

    input {
      display: none;
    }

    label {
      color: #666;
      background-image: url(~resource/images/icon_radio_off.png);
      background-size: 13px 13px;
      padding-left: 23px;
      font-size: 13px;
      display: inline-block;
      line-height: 100%;
      background-repeat: no-repeat;
      background-position: top left;

      &:hover {
        cursor: pointer;
      }
    }

    input:checked ~ label {
      color: #000;
      background-image: url(~resource/images/icon_radio_on.png);
    }
  }
}
