
@import "mediaQuery.scss";
.form {
  display: flex;
  width: 100%;
}

.form_number {
  position: relative;
  display: flex;
  width: 100%;

  input[type="number"] {
    @include mq(sm) {
      border: 0;
    }


    font-weight: 600;
    padding: 10px 50px 10px 10px;
    width: 100%;
    height: 33px;
    max-height: 33px;
    text-align: right;
    border-top: 1px solid #DDD;
    border-bottom: 1px solid #DDD;
    border-left: 1px solid #DDD;
    font-size: 13px;
    line-height: 100%;
    border-right: 0;
    background-color: #FAFAFA;
  }

  span {
    display: inline-block;
    position: absolute;
    line-height: 100%;
    font-size: 11px;
    color: #000;
    margin-bottom: 10px;
    margin-right: 10px;
    text-align: right;
    font-weight: 600;
    right: 0;
    bottom: 0;
  }
}

.form_button {
  position: relative;
  width: 60px;
  min-width: 60px;

  input[type="button"] {
    @include mq(sm) {
      border: 1px solid #000;
    }
    font-weight: 600;
    border-radius: 0 2px 2px 0;
    padding: 10px;
    height: 33px;
    max-height: 33px;
    line-height: 100%;
    color: #FFF;
    border: 1px solid #000;
    line-height: 100%;
    width: 100%;
    text-align: center;
    font-size: 13px;
    background-color: #000;

    &:hover {
      cursor: pointer;
    }
  }
}
