@import "mediaQuery.scss";

#header_box_r_wallet {
  @include mq() {
    display: flex;
    margin-left: 15px;

    input[type="checkbox"] {
      display: none;
    }

    label {
      border: 1px solid #066;
      display: inline-block;
      font-size: 13px;
      line-height: 100%;
      padding: 10px;
      background-color: rgba(0, 60, 60, 1);
      color: #BDD;
    }

    input:checked ~ label {
      border: 1px solid #099;
      background-color: rgba(0, 120, 120, 1);
      color: #FFF;
      transition: background-color 0.25s, color 0.25s, border 0.25s;
    }

    label {
      &:hover {
        cursor: pointer;
      }

      span {
        background-image: url(~resource/icons/metamask.svg);
        background-size: 15px 15px;
        background-repeat: no-repeat;
        padding-left: 23px;
      }
    }

    input:checked ~ div {
      width: 375px;
      transition: width 0.25s;
    }
  }
  @include mq(sm) {
    display: none;
  }
}

#header_box_r_wallet_text {
  width: 0;
  line-height: 0;
  background-color: rgba(0, 60, 60, 1);
  overflow: hidden;
  transition: width 0.25s;

  span {
    color: #BDD;
    border-top: 1px solid #066;
    border-right: 1px solid #066;
    border-bottom: 1px solid #066;
    width: 100%;
    display: inline-block;
    padding: 10px;
    font-size: 13px;
    line-height: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
}
