@import "mediaQuery.scss";

#header_box_r_balance {
  @include mq() {
    a {
      display: inline-block;
      background-color: rgba(0, 60, 60, 1);
      border: 1px solid #066;
      color: #BDD;
      padding: 10px;
      margin-left: 15px;
      text-decoration: none;
      font-size: 13px;
      line-height: 100%;
    }

    span {
      display: inline-block;
      background-image: url(~resource/images/icon_balance.png);
      background-size: 13px 13px;
      padding-left: 23px;
      background-repeat: no-repeat;
    }
  }
  @include mq(sm) {
    display: none;
  }
}
